<template>
  
  <div>
      <h3>Wallet</h3>
      <table class='table'>
        <tr>
          <th scope='col'>Net worth</th>
          <th>{{roundUp(netWorth)}} $</th>
        </tr>
        <tr>
          <th>USD</th>
          <td>{{roundUp(USD.amount)}} $</td>
        </tr>
        <tr v-for='asset in assets' v-bind:key='asset.id'>
          <th>{{asset.name}}</th>
          <td>{{roundUp(asset.value)}} $</td>
        </tr>
      </table>
  </div>
</template>

<script>
export default {
  name: 'Wallet',
  props: [
    'USD',
    'assets',
    'netWorth'
  ],
  methods: {
    roundUp (number) {
      var parsed = parseFloat (number);
      return parsed.toFixed (2);
    }
  }
}
</script>

