<template>
  <div>
    <h3>Trade assets</h3>

    <div class='input-group mb-3'>
      <select class="form-control" v-model="Selected">
        <option disabled='disabled' value>Select asset</option>
        <option v-for='asset in activeAssets' v-bind:key='asset.id'>
          {{asset.name}}
        </option>
      </select>

      <input 
      type='number' 
      class='form-control'
      v-model='tradedAmount' 
      placeholder="0$"
      step="100"
      id='tradeInput'
      min=0/>

      <button 
      type="button" 
      class="btn btn-success"
      v-on:click='buy'>
        Buy
      </button>
      &nbsp;
      <button 
      type="button" 
      class="btn btn-danger"
      v-on:click='sell'>
        Sell
      </button>
    </div>

  </div>
</template>

<script>
export default {
  name: 'BuySell',
  props: [
    'assets',
    'USD'
  ],
  data () {
    return {
      Selected: '',
      tradedAmount: ''
    }
  },
  computed: {
    activeAssets () {
      return Object.values (this.assets).filter (function (asset) {
        if (asset.price !== 0) {return asset}
      })
    }
  },
  methods: {
    buy () {
      if (Number (this.tradedAmount) > this.USD.amount) {
        alert ('Insufficient funds.');
      } else if (Number (this.tradedAmount) < 0) {
        alert ('Insert a positive value.');
      } else {
        let findAsset = this.assets [this.Selected];
        findAsset.value += Number (this.tradedAmount);
        sessionStorage [this.Selected] = findAsset.value;
        this.USD.amount -= Number (this.tradedAmount);
        sessionStorage.USD = this.USD.amount;
        this.tradedAmount = '';
      }
    },
    sell () {
      let findAsset = this.assets [this.Selected];
      if (Number (this.tradedAmount) >  findAsset.value) {
        alert ('Insufficient funds.');
      } else if (Number (this.tradedAmount) < 0) {
        alert ('Insert a positive value.');
      } else {
        findAsset.value -= Number (this.tradedAmount);
        sessionStorage [this.Selected] = findAsset.value;
        this.USD.amount += Number (this.tradedAmount);
        sessionStorage.USD = this.USD.amount;
        this.tradedAmount = '';
      }
    }
  }
}
</script>

