  43: "<h3 class='text-center'>Trump attacts China again</h3><br/><i>Not only has China declined to adopt promised reforms, it has embraced an economic model dependent on massive market barriers, heavy state subsidies, currency manipulation, product dumping technology transfers and the theft of intellectual property and also trade secrets on a grand scale</i>, said Trump. However Lu Ting, chief economist at Nomura Securities does not believe that safe-haven gold will profit from this weekening sentiment.",
  42: "<h3 class='text-center'>135,000 private-sector jobs created in September as U.S. hiring continues to slow</h3><br/><i>Businesses have turned more cautious in their hiring,</i> said Mark Zandi, chief economist of Moody’s Analytics. <i>Small businesses have become especially hesitant. If businesses pull back any further, unemployment will begin to rise.</i>",
  41: "<h3 class='text-center'>U.S. expands blacklist to include China's top AI startups ahead of trade talks</h3><br/> The U.S. government widened its trade blacklist to include some of China’s top artificial intelligence startups, punishing Beijing for its treatment of Muslim minorities and ratcheting up tensions ahead of high-level trade talks in Washington this week.",
  40: "<h3 class='text-center'>Trade talk between China and US scheduled for tomorrow</h3><br/> <i>Chances are that the two parties will reach a phase one deal,</i> Evelyn Cheng, Expert.",
  39: "<h3 class='text-center'>Alipay wants nothing to do with Bitcoin</h3><br/><i>Mainland China has strict laws banning gambling, and many still see cryptocurrency as such. Alipay and other providers still look at other cryptocurrency providers as investment outlets and not payment vehicles. Thus, until this perception changes, it is likely that current measures will stay in force.</i> Celine Lu, founder and CEO of BitDeer, a hashpower sharing platform.",
  38: "<h3 class='text-center'>Mark Zuckerberg in front of Congress tomorrow</h3><br/>Tomorrow, Facebook CEO Zuckerberg faces a questioning from the House Financial Services Committee. The hearing will be about Libra, Facebook’s proposed cryptocurrency. If Zuckerberg fails to convince Congress on the benefits of Libra and digital currencies, it may be seen as a blow to other crypto currencies as well.",
  37: "<h3 class='text-center'>Blockchain Core for Innovation in China</h3> Xi Jinping, President of China says the world's second biggest economy should accelerate the development of blockchain technology. <i>This positive comment on the backbone of cryptocurrencies will clearly influence crypto trading.</i>, Andy Cheung, head of operations of the cryptocurrency exchange OKEx.",
  36: "<h3 class='text-center'>November meeting of the Asia Pacific Economic Cooperation council cancelled</h3><br/> A partial trade deal between China and the U.S. was expected to be signed in this meeting. China's cabinet adviser Zhu Guangyao told Reuters on the sidelines of a forum in Singapore on China-U.S. relations that he is still optimistic that a deal can be signed next month.",
  35: "<h3 class='text-center'>China's Gold-Backed Crypto Would Be Bearish for Bitcoin</h3><br/> China launching a digital counterpart of the yuan backed by gold puts Bitcoin (BTC) at a disadvantage, veteran gold bug Peter Schiff claims. <i>A crypto backed by gold is much better than one backed by nothing!<i/>",
  34: "<h3 class='text-center'>China leaves bitcoin mining out of list of restricted activities</h3><br/> China, which is among the biggest manufacturers of bitcoin mining gear, has decided against eliminating the mining of the cryptocurrency.",
  33: "<h3 class='text-center'>Trump has little news on the trade deal</h3><br/> President Donald Trump says he has not agreed to rolling back tariffs as suggested by Beijing to achieve the first phase of their agreement. It remains unclear about when the much-anticipated phase one of a U.S.-China trade deal will be signed.",
  32: "<h3 class='text-center'>Chinese Regulators Set to Push Inspections of Crypto Mining Firms</h3><br/> Regulators in the Chinese autonomous province of Inner Mongolia intend to dispatch inspection units to assure the clean-up of local crypto mining companies.",
  31: "<h3 class='text-center'>Chinese police shut down the Shanghai offices of cryptocurrency exchange Binance</h3><br/> Recently, financial authorities in China issued a notice to the public, directing individuals to report businesses engaged in virtual asset trading to the Shanghai headquarters of the People's Bank of China.",
  30: "<h3 class='text-center'>Canaan now listed on Nasdaq</h3><br/> <i>We have a good story that resonates with investors for the long-term,</i> N.G. Zhang, founder of Canaan.",
  29: "<h3 class='text-center'>Canaan stocks will continue to tumble</h3><br/> Johnson and Smith financial holdings predicts that prices of Canaan stocks will not remain at their current level for the forseeable future.",
  28: "<h3 class='text-center'>Uncertainty about the US-China trade deal</h3><br/> US President Donald Trump suggested that the long-awaited trade deal might have to wait until the 2020 election.",
  27: "<h3 class='text-center'>Canaan, cryptocurrency mining equipment manufacturer is struggling to survive its business.</h3>",
  26: "<h3 class='text-center'>China's bitcoin miners scoop up greater production power</h3><br/> China's bitcoin miners now control two-thirds of the crypto network's processing power, a growing share that is likely to benefit the country's miners.",
  25: "<h3 class='text-center'>U.S. House of Representatives votes to impeach President Donald Trump</h3>",
  24: "<h3 class='text-center'>China’s central bank official says digital yuan will be different from Bitcoin and stablecoins.</h3><br/> <i>The currency is not for speculation. It is different to bitcoin or stable tokens, which can be used for speculation or require the support of a basket of currencies.</i> People’s Bank of China deputy director Mu Changchun.",
  23: "<h3 class='text-center'>Chinese Bitcoin mining farms under pressure to scale down operations due to electricity shortages</h3><br/> Nearly 7,000 crypto mining machines were seized.",
  22: "<h3 class='text-center'>Mining giant Bitmain may lay off another 50% of staff</h3><br/> Bitcoin giant endures a second wave of massive layoffs after the first in December 2018. It is speculated that this is due to Bitcoin cutting the number of coins awarded to miners in half.",
  21: "<h3 class='text-center'>Early new year movement in cryptocurrency value shows steady increase</h3><br/> A sustained decrease in value, beginning October 2019 is now showing signs of reversal.",
  20: "<h3 class='text-center'>First death associated with sickness prevalent among communities in China</h3><br/> Chinese state media reports the first known death from a new virus that has infected dozens of people in China and sets off worries across Asia.",
  19: "<h3 class='text-center'>World's two biggest economies sign phase one trade deal</h3><br/> China and the US make progress in their trade talks. However, there are still doubts whether a cordial relationship can be mantained.",
  18: "<h3 class='text-center'>Mondays are the best days to buy bitcoin</h3><br/> Studies show that Bitcoin’s unique features reveal an opposite behavior to traditional stock markets, showing a positive average return on Mondays when considering wider time periods.",
  17: "<h3 class='text-center'>Chinese authorities call upon people to reduce mobility</h3><br/> <i>The Chinese New Year celebrations start this coming weekend, which usually results in roughly 300 million people traveling inside China. Reducing that dramatically would remove a tremendous annual stimulus for the Chinese economy,</i> a Zaner Group Analyst says.",
  16: "<h3 class='text-center'>WHO says <i>too early</i> to declare coronavirus outbreak in China a global emergency</h3><br/> 584 cases have now been reported to WHO, including 17 deaths, mostly in China, but also in Japan, South Korea, Singapore, Thailand, the United States and Vietnam. WHO does not recommend any broader restrictions on travel or trade, and recommends exit screening at airports as part of a comprehensive set of containment measures.",
  15: "<h3 class='text-center'>Researchers find two new drugs that can effectively inhibit coronavirus</h3><br/> Preliminary tests showed that two drugs - Abidol and Darunavir – can effectively inhibit the virus in vitro cell experiments, according to Li Lanjuan, Professor at Zhejiang University. The WHO, however, sais that <i>there are no known effective therapeutics against this 2019-nCoV.</i>",
  14: "<h3 class='text-center'>BTCS expands business model with development of digital asset data analytics platform</h3><br/> BTCS’s analytics platform allows users to aggregate data related to their holdings in near real-time in a single view combined with a robust suite of tools for performance tracking.",
  13: "<h3 class='text-center'>Chinese crypto mine suffer under Coronavirus outbreak</h3><br/> Bitmain and Canaan Creative are the main mining chip manufacturers in China. Yet due to the outbreak of the virus, these companies have published notices on their websites of delays in their after-sale services until Feb 10.",
  12: "<h3 class='text-center'>Bitcoin mining shifting from China to North America</h3><br/> The world's biggest cryptocurrency asset manager said it was seeing efforts to shift bitcoin production to North America from China, which dominates digital coin mining.",
  11: "<h3 class='text-center'>Stock market crash, panic in the markets</h3><br/> A devastating week for the stock markets that close with double-digit declines, an unusual characteristic that links them to the cryptocurrency markets",
  10: "<h3 class='text-center'>Institutions are turning away from gold to Bitcoin</h3><br/> The price of gold saw an uptick in the first week of February, as did the price of Bitcoin (BTC), which rose above $10,000 for the first time this year.",
  9: "<h3 class='text-center'>South Korea raises alert level to maximum, Italy and Iran cases spike</h3><br/> South Korea’s new cases jumped by 169, bringing the country’s total to 602 infected, according to the country’s for Centers for Disease Control and Prevention. Infections in Italy saw a sharp spike, surging to more than 130 cases by yesterday.",
  8: "<h3 class='text-center'>Warren Buffett doesn’t want to own any cryptocurrency</h3><br/> The fourth-wealthiest person in the world doesn’t think cryptocurrencies have anything to offer.",
  7: "<h3 class='text-center'>Surprise half-point cut to the Federal Reserve’s key interest rate</h3><br/> Fed is expectated to be unable to resist political pressure to cut further as the coronavirus spreads across the U.S. The cut was immediately followed by a tweet from President Donald Trump calling on the Fed to do more.",
  6: "<h3 class='text-center'>Canaan bitcoin mining manufacturer accused of misleading IPO investors</h3><br/> Phillippe Lemieux, a Canaan investor claims the company violated the Securities Act of 1933 by misleading investors and the Securities and Exchange Commission (SEC) in regards to the company’s financial wellbeing.",
  5: "<h3 class='text-center'>Oil crashes by most since 1991 as Saudi Arabia launches price war</h3><br/> US oil prices crashed as much as 34% after Saudi Arabia shocked the market by launching a price war against onetime ally Russia.",
  4: "<h3 class='text-center'>WHO declares coronavirus a 'pandemic', Trump suspends travel from Europe to US</h3><br/> WHO Director-General, Dr. Tedros Adhanom Ghebreyesus, noted that over the past 2 weeks, the number of cases outside China increased 13-fold and the number of countries with cases increased threefold.",
  3: "<h3 class='text-center'>Sell off across markets</h3><br/> Global leaders are striking a pessimistic tone over the likely economic impact of the coronavirus outbreak. The gold price declines as investors raise cash to cover losses in other markets.",
  2: "<h3 class='text-center'>Chinese citizens are returning to shops as the COVID-19 outbreak eases</h3><br/> Whilst the virus is forcing shops to close across Europe and America, China is finally reopening businesses after a long few months of quarantine. Retail sales for the world’s second-largest economy shrank by a fifth in the first two months of 2020 from a year earlier.",
  1: ''
}

var stockData = {
  'Bitcoin': bitcoinData,
  'Gold': goldData,
  'EOS': eosData,
  'Canaan': canaanData,
  'BTCS': btcsData,
  'Yuan': yuanData
  };
var numberOfDays = Object.keys (bitcoinData.date).length - 1;

export default {
  name: 'Home',
  components: {
    BuySell,
    StockState,
    Wallet,
    DateComponent,
    NewsComponent
  },
  data () {
    return {
      assets: {
        'Yuan': {
          name: 'Yuan',
          id: 0,
          value: 0,
          price: yuanData.price [numberOfDays],
          change: yuanData.change [numberOfDays]
        },
        'Bitcoin': {
          name: 'Bitcoin',
          id: 1,
          value: 0,
          price: bitcoinData.price [numberOfDays],
          change: goldData.change [numberOfDays]
        },
        'Gold': {
          name: 'Gold',
          id: 2,
          value: 0,
          price: goldData.price [numberOfDays],
          change: goldData.change [numberOfDays]
        },
        'EOS': {
          name: 'EOS',
          id: 3,
          value: 0,
          price: eosData.price [numberOfDays],
          change: eosData.change [numberOfDays]
        },
        'BTCS': {
          name: 'BTCS',
          id: 4,
          value: 0,
          price: btcsData.price [numberOfDays],
          change: btcsData.change [numberOfDays]
        },
        'Canaan': {
          name: 'Canaan',
          id: 5,
          value: 0,
          price: canaanData.price [numberOfDays],
          change: canaanData.change [numberOfDays]
        },
      },
      USD: {amount: 5000},
      netWorth: 5000,
      dayToday: {
        today: bitcoinData.date [numberOfDays],
        numberOfDays: numberOfDays,
        news: allNews [numberOfDays],
      },
      counter: numberOfDays,
      nrOfDays: numberOfDays
    }
  },
  mounted () {
    for (var asset in this.assets) {
      if (sessionStorage [asset]) {
        this.assets [asset] ['value'] = Number (sessionStorage [asset]);
      }
    }
    if (sessionStorage.USD) {
      this.USD.amount = Number (sessionStorage.USD);
    }
    if (sessionStorage.counter) {
      this.counter = Number (sessionStorage.counter);
      this.dayToday.today = bitcoinData.date [this.counter];
      for (asset in this.assets) {
        this.assets [asset] ['price'] = stockData [asset] ['price'] [this.counter];
        this.assets [asset] ['change'] = stockData [asset] ['change'] [this.counter];
      }
    }
    if (sessionStorage.netWorth) {
      this.netWorth = Number (sessionStorage.netWorth);
    }
    if (sessionStorage.news) {
      this.dayToday.news = sessionStorage.news;
    }
  },
  watch: {
    counter (newCounter) {
      sessionStorage.counter = newCounter;
    }
  },
  methods: {
    roundUp (number) {
    var parsed = parseFloat (number);
    return parsed.toFixed (2);
    },
    nextDay () {
      this.counter -= 1;
      this.dayToday.today = bitcoinData.date [this.counter];
      this.dayToday.news = allNews [this.counter];
      sessionStorage.news = this.dayToday.news;
      this.netWorth = this.USD.amount;
      for (var asset in stockData) {
        if (this.assets [asset] ['price'] !== 0) {
          this.assets [asset] ['value'] = this.assets [asset] ['value'] / stockData [asset] ['price'] [this.counter + 1] * stockData [asset] ['price'] [this.counter];
          this.assets [asset] ['value'] = parseFloat (this.assets [asset] ['value'].toFixed (2));
          this.netWorth += this.assets [asset] ['value'];
          sessionStorage.netWorth = this.netWorth;
          sessionStorage [asset] = this.assets [asset] ['value'];
        }
        this.assets [asset] ['price'] = stockData [asset] ['price'] [this.counter];
        this.assets [asset] ['change'] = stockData [asset] ['change'] [this.counter];
      }
    }
  }
}
</script>