<template>
  <div>
      <table class='table'>
        <tr>
            <td scope='col'>Asset</td> 
            <th scope='col'>Price</th>
            <th scope='col'>Change</th>
        </tr>
        <tr v-for='asset in assets' v-bind:key='asset.id'>
            <th scope='row'>{{asset.name}}</th>
            <td v-if='asset.price !== 0' v-bind:style='{color: applyColor (asset)}'>{{fourDigits(asset.price)}} $</td>
            <td v-else><i>Not listed</i></td>
            <td v-if='asset.change == null'></td>
            <td v-else v-bind:style='{color: applyColor (asset)}'>{{roundUp(asset.change)}} %</td>

        </tr>
        </table>
  </div>
</template>

<script>

export default {
  name: 'StockState',
  components: {
  },
  props: ['assets'],
  methods: {
    applyColor (asset) {
        if (asset.change>0) {
            return 'green';
        } else if (asset.change<0) {
            return 'red';
        }
    },
    roundUp (number) {
      var parsed = parseFloat (number);
      return parsed.toFixed (2);
    },
    fourDigits (number) {
      var parsed = parseFloat (number);
      if (parsed > 10000) {
        return parsed.toPrecision (5)
      } else {
        return parsed.toPrecision (4)
      }
    }
  }
}
</script>

